import { HttpMethod } from "../types/index.js";
export default function (provider) {
  return {
    addParticipants(id) {
      return provider.send(`/conversations/${id}/participants/ADD`, HttpMethod.POST);
    },

    createConversation(body) {
      return provider.send("/conversations", HttpMethod.POST, body);
    },

    exitConversation(id) {
      return provider.send(`/conversations/${id}/exit`, HttpMethod.POST);
    },

    getConversation(id) {
      return provider.send(`/conversation/${id}`, HttpMethod.GET);
    },

    joinConversation(codeId, body) {
      return provider.send(`/conversations/${codeId}/join`, HttpMethod.POST, body);
    },

    muteConversation(id, body) {
      return provider.send(`/conversations/${id}/mute`, HttpMethod.POST, body);
    },

    removeParticipants(id, body) {
      return provider.send(`/conversations/${id}/participants/REMOVE`, HttpMethod.POST, body);
    },

    rotateConversation(id) {
      return provider.send(`/conversations/${id}/rotate`, HttpMethod.POST);
    },

    updateConversation(id, body) {
      return provider.send(`/conversation/${id}`, HttpMethod.POST, body);
    },

    updateConversationRole(id, body) {
      return provider.send(`/conversation/${id}/participants/ROLE`, HttpMethod.POST, body);
    }

  };
}