import { ethers, utils } from "ethers";
import { parse, stringify } from "uuid";
import { RegistryAddress, BridgeAddress, RpcProvider } from "./constants.js";
import { AssetABI, RegistryABI, BridgeABI } from "./abis/index.js";
export default class ContractOpt {
  constructor(provider) {
    this.signer = void 0;
    this.contractEntries = [];
    this.registryContract = new ethers.Contract(RegistryAddress, RegistryABI, new ethers.providers.JsonRpcProvider(RpcProvider));
    this.signer = provider.getSigner();
  }

  async getContractAddressByAssetId(assetId) {
    const record = this.contractEntries.find(({
      asset_id
    }) => asset_id === assetId);
    if (record) return record.contract_address;
    const address = await this.registryContract.contracts(utils.hexlify(parse(assetId)));
    this.contractEntries.push({
      asset_id: assetId,
      contract_address: address
    });
    return address;
  }

  async getAssetIdByContractAddress(address) {
    const record = this.contractEntries.find(({
      contract_address
    }) => contract_address === address);
    if (record) return record.asset_id;

    try {
      const resp = await this.registryContract.assets(address);
      const assetId = stringify(utils.arrayify(resp));
      this.contractEntries.push({
        asset_id: assetId,
        contract_address: address
      });
      return assetId;
    } catch (error) {
      return "";
    }
  }

  async execAssetContract(assetId, method, args) {
    const address = await this.getContractAddressByAssetId(assetId);
    const contract = new ethers.Contract(address, AssetABI, this.signer);
    return contract[method](...args, {
      gasPrice: 10000000 // 0.01 Gwei

    });
  }

  execBridgeContract(method, args, value) {
    const contract = new ethers.Contract(BridgeAddress, BridgeABI, this.signer);
    return contract[method](...args, {
      gasPrice: 10000000,
      // 0.01 Gwei
      value
    });
  }

}