import { utils } from "ethers";
export function fmtWithdrawAmount(amount, isNative = false) {
  return utils.parseUnits(amount, isNative ? 18 : 8).toString();
}
export function fmtBalance(amount, isNative = false) {
  return utils.formatUnits(amount, isNative ? 18 : 8).toString();
}
export function wrapPromiseWithTimeout(promise, delay = 30000) {
  if (!promise) return;
  return Promise.race([promise, new Promise((resolve, reject) => {
    setTimeout(() => {
      reject(new Error("Operation Timeout"));
    }, delay);
  })]);
}