import { HttpMethod } from "../types/index.js";
export default function (provider) {
  return {
    getCollectibleOutputs(params) {
      return provider.send("/collectibles/outputs", HttpMethod.GET, "", params);
    },

    getCollectibleToken(id) {
      return provider.send(`/collectibles/tokens/${id}`, HttpMethod.GET);
    },

    sendCollectibleAction(id, action, pin) {
      return provider.send(`/collectibles/requests/${id}/${action}`, HttpMethod.POST, {
        pin
      });
    },

    sendCollectibleRequest(body) {
      return provider.send("/collectibles/requests", HttpMethod.POST, body);
    }

  };
}