import metamask from "./metamask.js";
import walletconnect from "./walletconnect.js";
import onekey from "./onekey.js";
export default async function (type, config) {
  if (type === "metamask") {
    return metamask();
  } else if (type === "onekey") {
    return onekey();
  } else {
    return walletconnect(config.infuraId);
  }
}