/* eslint-disable @typescript-eslint/no-unused-vars */
import axios from "axios";
import DEFAULTS from "../defaults.js";
export default class HttpProvider {
  constructor(endpoint = DEFAULTS.HTTP_URL, headers = {}) {
    this.axios = void 0;
    this.axios = axios.create({
      baseURL: endpoint,
      headers
    });
    this.axios.interceptors.response.use(response => {
      if (response.data.error) {
        const {
          code,
          description
        } = response.data.error;
        return Promise.reject(new Error(`${code}: ${description}`));
      }

      return Promise.resolve(response);
    });
  }

  get hasSubscriptions() {
    return false;
  }

  get isConnected() {
    return true;
  }

  get instance() {
    return this.axios;
  }

  async connect() {// noop
  }

  async disconnect() {// noop
  }

  clone() {
    throw new Error("Unimplemented");
  }

  on(type, sub) {
    console.error("HTTP Provider does not have 'on' emitters, use WebSockets instead");
    return () => {//
    };
  }

  async send(path, method, body, query) {
    const response = await this.axios.request({
      data: body,
      method,
      params: query,
      url: path
    });
    return response.data.data;
  }

  subscribe(types, method, params, cb) {
    throw new Error("HTTP Provider does not have subscriptions, use WebSockets instead");
  }

  unsubscribe(type, method, id) {
    throw new Error("HTTP Provider does not have subscriptions, use WebSockets instead");
  }

}